.gradient {
  width: 150px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 10px 10px white;
  border-radius: 5px;
  position: relative;
  margin-top: 50px;

  .overlay {
    width: 144px;
    height: 44px;
    background-color: #ccc;
    border-radius: 5px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1rem;
    font-weight: bold;

    .text {
      position: absolute;
      width: 150px;
      height: 50px;
      color: transparent;
      background-clip: text;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}
