.container {
  width: 271px;
  height: 478px;
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  margin-left: calc(30% - 135.5px);
  margin-right: calc(30% - 135.5px);
  margin-top: calc(275px - 239px);

  p {
    padding: 15px;
    color: white;
    height: 30px;
    width: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 3px;
    cursor: pointer;
  }

  p:first-of-type {
    font-family: "Matemasie";
    width: 103px;
    cursor: auto;
    font-size: 14px;
  }
}
